.tkdcard {
  border-radius: 10px;
  margin: 9px;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 50px rgb(0 0 0 / 15%);
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  animation: 0.7s ease 0s 1 normal none running zoomin;
  color: var(--text-color);
  height: auto;
}
.tkdcard::before {
}
.tkdcard .tkdcard-content {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  padding: 15px;
  z-index: 100;
}
.tkdcard .image {
  width: 140px;
  height: 100%;
  display: flex;
  border-radius: 10px;
  margin-right: 10px;
  padding: 7px;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 50px rgb(0 0 0 / 15%);
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  justify-content: center;
  align-items: center;
}
.tkdcard .image img {
  width: 80%;
  height: auto;
  filter: drop-shadow(var(--tkd2) 0 0 0.5rem);
}
.tkdcard .name-profession {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tkdcard .name-profession .name {
  font-size: 20px;
  font-weight: 600;
}
.tkdcard .name-profession .profession {
  font-size: 15px;
  font-weight: 500;
}
.tkdcard .name-profession .desc {
  font-size: 15px;
  font-weight: 500;
}
.tkdcard .button {
  display: flex;
  justify-content: space-around;
  background: none !important;
  box-shadow: none;
  margin: auto;
}
.tkdcard .button a {
  outline: none;
  border: none;
  padding: 8px 22px;
  cursor: pointer;
  border-radius: 0 10px 0 10px;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 50px rgb(0 0 0 / 15%);
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  position: absolute;
  top: -1px;
  right: 0;
}
.card_right {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.chart {
  display: flex;
  align-items: center;
  border-radius: 10px;
  margin: 10px;
  padding: 7px;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 50px rgb(0 0 0 / 15%);
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
}
.chart i {
  margin-right: 8px;
}
.Carddiv1 {
  width: 100%;
  flex-direction: column;
  display: flex;
}
.Carddiv2 {
  align-items: center;
  display: flex;
  justify-content: center;
}
@media (max-width: 500px) {
  .tkdcard .name-profession .desc {
    display: none;
  }
  .tkdcard .tkdcard-content {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
}