.content-body {
  margin: 56px 41px 30px 0;
  padding-bottom: 86px;
  overflow: auto;
  height: 100vh;
}
.content-body1 {
  margin: 0;
  padding: 0;
}
.form-head {
  padding: 6px 14px;
  z-index: 202;
  border-radius: 20px;
  box-shadow: 0 0 20px 4px #2c0101, inset 0 0 11px 0 #2c0101;
  border: 2px solid var(--tkd2);
  background-image: linear-gradient(to top, #d8d9db 0, #fff 80%, #fdfdfd 100%);
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-evenly;
  text-align: center;
}
.form-head_div {
  display: flex;
  align-items: center;
  color: var(--tkd1);
  text-shadow: 0 0 4px #2c0101, 0 0 12px #2c0101, 0 0 7px #2c0101;
}
.breadcrumb-item a {
  color: var(--tkd1);
}
.Profile_detail_icon_img {
  border-radius: 50%;
  box-shadow: inset 0 -6px 16px rgb(0 0 25 / 45%),
    inset 0 6px 16px rgb(255 255 220 / 25%),
    inset 0 -12px 32px rgb(0 0 25 / 55%),
    inset 0 12px 32px rgb(255 255 220 / 35%),
    inset 0 -32px 64px rgb(0 0 25 / 45%),
    inset 0 32px 64px rgb(255 255 220 / 25%), 0 8px 16px rgb(0 0 25 / 45%),
    0 16px 32px rgb(0 0 25 / 45%);
  height: 45px;
  width: 45px;
  margin-right: 20px;
}
.Profile_detail_icon_img div {
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 50%;
}
.Profile_detail_icon_img div img {
  width: 100%;
  height: 131%;
  transform: scale(1.1);
}
.header-profile {
  font-size: 16px;
  display: flex;
  align-items: center;
  padding: 14px;
  border-radius: 20px;
  background: var(--tkd2);
  box-shadow: 0 5px 5px rgb(0 0 0 / 40%);
  color: var(--tkd1);
  margin-right: 12px;
}
.header-profile i {
  font-size: 22px;
  margin-right: 5px;
}
.header-profile:after {
  position: absolute;
  content: "";
  border-left: 10px solid var(--tkd2);
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  position: relative;
  top: 0;
  right: -24px;
}
.css404 {
  height: 280px;
  position: relative;
  margin: auto;
  width: 580px;
  background-color: var(--tkd1);
  border-radius: 20px;
  box-shadow: 0 0 20px 4px var(--tkd2), inset 0 0 20px 4px var(--tkd2);
  border: 5px solid var(--tkd2);
  animation: 0.7s ease 0s 1 normal none running zoomin;
  padding: 10px;
  user-select: none;
}
.css404 a {
  width: 300px;
}
.css404 .arrowright {
  position: relative;
  width: 4em;
  left: -2em;
}
.css404 a .arrowright::before {
  content: "\f12c";
  position: absolute;
  width: 2em;
  height: 2.4em;
  background-color: #2c0101;
  color: #fe6100;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.2s;
  font-family: bootstrap-icons !important;
  font-style: normal;
  font-weight: normal !important;
  font-variant: normal;
  text-transform: none;
  vertical-align: -0.125em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.css404 a:hover .arrowright::before {
  content: "\f12f";
  right: 0;
  width: 4em;
}
.sidebar::-webkit-scrollbar {
  width: 0;
}
.sidebar-brand-icon {
  width: 0;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 8px;
}
.sidebar.toggled .sidebar-brand-icon {
  width: 60px;
}
.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 78px;
  padding: 6px 14px;
  z-index: 1000;
  overflow: visible;
  background: var(--tkd3);
  border: 3px solid #2c0101;
  background: #fe6100;
  box-shadow: 0 0 20px 4px #2c0101, inset 0 0 11px 0 #2c0101;
  border-left: none;
  border-bottom-right-radius: 20px;
  border-top-right-radius: 20px;
}
.sidebar.toggled {
  width: 17rem;
}
.sidebar .sidebar-brand-text {
  line-height: 23px;
  font-family: Julius Sans One;
  font-weight: bolder;
  text-align: center;
  letter-spacing: -1px;
  text-shadow: 0 4px 4px #282828;
  -webkit-text-stroke: 1px #f8f8f8;
}
.sidebar.toggled .sidebar-brand-text {
  display: inline;
  opacity: 1;
  font-size: 25px;
}
.sidebar .sidebar-brand-text {
  display: none;
  opacity: 0;
  font-size: 0;
}
.sidebar .logo-details {
  display: flex;
  position: relative;
  justify-content: space-around;
  align-items: center;
}
.sidebar .logo-details .icon {
  opacity: 0;
}
.sidebar .logo-details .logo_name {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  opacity: 0;
}
.sidebar.toggled .logo-details .icon,
.sidebar.toggled .logo-details .logo_name {
  opacity: 1;
}
.sidebar .logo-details #btn {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  font-size: 22px;
  font-size: 23px;
  text-align: center;
  cursor: pointer;
}
.sidebar.toggled .logo-details #btn {
  text-align: right;
}
.sidebar i {
  color: #fff;
  height: 60px;
  min-width: 50px;
  font-size: 28px;
  text-align: center;
  line-height: 60px;
}
.sidebar ul {
  height: 78vh;
}
.sidebar li {
  position: relative;
  margin: 8px 0;
  height: 46px;
}
.sidebar li .sidebara {
  transform: scale(1);
}
.sidebar li:hover .sidebara {
  transform: scale(1.1);
}
.sidebar li:active .sidebara {
  transform: scale(0.9);
}
.sidebar li.profile:hover {
  transform: none;
}
.sidebar li.profile:active {
  transform: none;
}
.sidebar li .tooltip {
  position: relative;
  top: -23px;
  left: calc(100%+26px);
  z-index: 1001;
  background: #fff;
  box-shadow: 0 0 10px #2c0101;
  padding: 12px;
  border-radius: 20px;
  font-size: 15px;
  font-weight: 400;
  opacity: 0;
  white-space: nowrap;
  transform: translate(-45%, -50%) scale(0);
  transform-origin: left;
  width: max-content;
}
.sidebar li:hover .tooltip {
  opacity: 1;
  transform: translate(0%, -50%) scale(1);
}
.sidebar li:hover .tooltip::before {
  content: "";
  position: absolute;
  background: inherit;
  width: 15px;
  height: 15px;
  transform: rotate(45deg);
  left: -5px;
  top: 32%;
  z-index: -1;
}
.sidebar li:hover .dropmenu::before {
  top: 47%;
}
.sidebar.toggled li .tooltip {
  display: none;
}
.sidebar input {
  font-size: 15px;
  color: #fff;
  font-weight: 400;
  outline: none;
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  border-radius: 20px;
  border: 2px solid #2c0101;
  background: #fe6100;
  box-shadow: 0 0 20px 4px #2c0101, inset 0 0 11px 0 #2c0101;
  justify-content: center;
  padding: 0 0 0 46px;
}
.sidebar.toggled input {
  padding: 0 20px 0 50px;
  width: 100%;
}
.sidebar li a {
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(5px);
  background: hsla(0, 0%, 100%, 0.1);
  border: 1px solid hsla(0, 0%, 100%, 0.5);
  border-bottom: none;
  border-right: none;
  box-shadow: 0 20px 50px rgb(0 0 0 / 15%);
  color: var(--text-color);
}
.sidebar.toggled li a {
}
.sidebar li a .links_name,
.sidebar li a .links_name1 {
  text-align: center;
  color: #fff;
  font-family: Julius Sans One;
  text-shadow: 0 4px 4px #282828;
  font-weight: 900;
}
.sidebar li a .links_name {
  display: none;
  transform: scale(0);
  transform-origin: left;
  font-size: 0;
}
.sidebar.toggled li a .links_name {
  display: block;
  transform: scale(1);
  font-size: 13px;
}
.sidebar li i {
  height: auto;
  font-size: 18px;
}
.sidebar li.profile {
  position: fixed;
  height: 60px;
  width: 78px;
  left: 0;
  bottom: -8px;
  overflow: hidden;
  display: flex;
  border: 3px solid #2c0101;
  background: #fe6100;
  box-shadow: 0 0 20px 4px #2c0101, inset 0 0 11px 0 #2c0101;
  border-bottom-right-radius: 20px;
}
.sidebar.toggled li.profile {
  width: 17rem;
}
.sidebar.toggled li .profile-details {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  margin-left: 8px;
}
.sidebar li .profile-details {
  display: none;
}
.name_job {
  display: flex;
  align-items: center;
  padding: 3px 24px;
  border-radius: 20px;
  background: var(--tkd2);
  box-shadow: 0 5px 5px rgb(0 0 0 / 40%);
  color: #fe6100;
  flex-direction: column;
  position: relative;
  transform: translateX(-17%) scale(0);
  transform-origin: left;
}
.name_job::before {
  content: "";
  position: absolute;
  background: inherit;
  width: 18px;
  height: 18px;
  transform: rotate(45deg);
  left: -7px;
  top: 14px;
  z-index: -1;
}
.sidebar.toggled .name_job {
  transform: translateX(0) scale(1);
}
.sidebar li.profile .name,
.sidebar li.profile .job {
  font-size: 15px;
}
.sidebar li.profile .job {
  font-size: 12px;
}
.sidebar .profile #log_out {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.sidebar.toggled .profile #log_out {
  right: 3px;
}
.sidebar .profile #log_out i {
  font-size: 22px;
  width: 100%;
  height: 100%;
  line-height: 45px;
}
.home-section {
  position: relative;
  background: #e4e9f7;
  min-height: 100vh;
  top: 0;
  left: 78px;
  width: calc(100% - 78px);
  z-index: 2;
}
.sidebar.toggled ~ .home-section {
  left: 250px;
  width: calc(100% - 250px);
}
.home-section .text {
  display: inline-block;
  color: #11101d;
  font-size: 25px;
  font-weight: 500;
  margin: 18px;
}
.sidebar .sidebarToggleTop {
  margin: 0;
  border-radius: 10px;
  background-image: linear-gradient(to top, #d8d9db 0%, #fff 80%, #fdfdfd 100%);
  border: 1px solid #8f9092;
  box-shadow: inset 0 4px 3px 1px #fcfcfc, inset 0 6px 8px #d6d7d9,
    inset 0 -4px 4px #cecfd1, inset 0 -6px 4px #fefefe, inset 0 0 3px 0 #cecfd1,
    0 0 5px 0 #2c0101;
  cursor: pointer;
  padding: 2px 0.5rem 0;
}
.sidebar.toggled .sidebarToggleTop {
  margin-left: 12px;
}
.sidebar .nav-item .collapse .collapse-inner,
.sidebar .nav-item .collapsing .collapse-inner {
  padding: 0.5rem 0;
  min-width: 10rem;
  font-size: 0.85rem;
  margin: 0 0 1rem 0;
  max-height: 140px;
  overflow: auto;
}
.collapse-inner::-webkit-scrollbar {
  width: 0;
}
.dropmenu ul {
  height: auto;
  margin: 0;
  width: 100%;
}
.sidebar li .tooltip.dropmenu {
  padding: 10px;
}
.dropmenu li {
  margin: 0 0 8px;
  height: 36px;
  width: 100%;
}
.dropmenu ul li:last-child {
  margin: 0;
}
.dropmenu li a {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  margin-right: 10px;
}
.sidebar .has-arrow::after {
  content: "\f105";
  transform-origin: top;
  top: 50%;
  transform: rotate(0deg) translate(1000%, -50%) scale(0);
  position: absolute;
  right: 32px;
  font-size: 20px;
  color: var(--tkd1);
  text-shadow: 0 0 4px #2c0101, 0 0 12px #2c0101, 0 0 7px #2c0101;
}
.sidebar.toggled .has-arrow::after {
  transform: rotate(0deg) translate(0%, -50%) scale(1);
}
.sidebar li.sidebarli .tooltip {
  display: block;
}
.modal-footer button {
  margin: 0;
  color: var(--tkd1);
  position: absolute;
  bottom: -1px;
  left: -1px;
  background-color: var(--tkd2);
  border-radius: 0;
  border-top-right-radius: 20px;
  border-bottom-left-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  box-shadow: -8px 3px 10px -5px rgb(0 0 0 / 40%),
    8px 3px 10px -5px rgb(0 0 0 / 40%), 0 2px 5px -3px rgb(0 0 0 / 50%);
}
.modal-footer a {
  margin: 0;
  border-radius: 0;
  color: var(--tkd1);
  position: absolute;
  bottom: -1px;
  right: -1px;
  padding: 0.375rem 0.75rem;
  background-color: var(--tkd2);
  border-top-left-radius: 20px;
  border-bottom-right-radius: 10px;
  box-shadow: -8px 3px 10px -5px rgb(0 0 0 / 40%),
    8px 3px 10px -5px rgb(0 0 0 / 40%), 0 2px 5px -3px rgb(0 0 0 / 50%);
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.sidebar li.active a {
  border: 2px solid #0c0;
  transform: scale(1.05);
  background: #0c0;
  z-index: 10;
  position: relative;
  box-shadow: 0 0 20px 4px #2c0101, inset 0 0 11px 0 #2c0101;
}
.sidebar li.active a i {
  text-shadow: 0 0 4px #0c0, 0 0 12px #0c0, 0 0 7px #0c0;
  color: var(--tkd2);
}
.sidebar li.active a .links_name {
  text-shadow: 0 4px 4px #0c0;
}
.sidebar li.active .tooltip {
  color: #0c0;
  text-shadow: 0 0 #0c0;
}
.bodydark .sidebar {
  border-color: #fe6100;
}
.bodydark .sidebar li.profile {
  border-color: var(--tkd1);
}
.bodydark .iconheading:before {
  border-color: var(--tkd1) transparent;
  box-shadow: 0 0 7px 0 #2c0101, inset 0 0 11px 0 #2c0101;
}
