.tablink {
  -webkit-user-select: none;
  user-select: none;
  transition: all 0.2s ease;
  font-weight: 600;
  transition: box-shadow 0.1s ease, height 0.3s ease;
  height: 35px;
  float: left;
  outline: none;
  width: 20%;
  border: none;
  position: relative;
  font-size: 17px;
  font-weight: 400;
