.tablink::before {
  opacity: 0;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 3px;
  width: 0;
  background-color: var(--active-color);
  border-radius: 12px;
  transition: all 0.4s ease;
